.btn {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  min-width: 180px;
  min-height: 45px;
  padding: 14px 12px;
  text-transform: uppercase;
  text-align: center;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: bold;
  color: #ffffff;
  letter-spacing: 0.05em;
  cursor: pointer;
  border: 1px solid #ffffff;
  border-radius: 5px;
  background: transparent; }

.btn--outline {
  display: inline-block;
  max-width: 180px;
  margin: 0 0 0 50px; }

.btn--white {
  display: inline-block;
  max-width: 180px;
  color: #00b2ff;
  border-color: #00b2ff; }

.btn--white:hover, .btn--white:focus,
.btn--outline:hover,
.btn--outline:focus {
  background-color: #00b2ff;
  color: #ffffff; }

.btn--question {
  min-width: 270px;
  min-height: 45px;
  padding: 14px 18px;
  margin-right: 38px; }

.btn--submit {
  width: 100%; }

.btn--question:hover, .btn--question:focus,
.btn--submit:hover,
.btn--submit:focus {
  box-shadow: 4px 4px rgba(0, 0, 0, 0.25); }

.btn--blue {
  display: inline-block;
  padding: 18px 35px;
  margin-left: 96px;
  max-width: 406px;
  width: 100%;
  background-color: #00b2ff;
  border-color: transparent;
  font-size: 16px; }
  .btn--blue:hover, .btn--blue:focus {
    background-color: transparent;
    border: 1px solid #00b2ff; }

/* Tablet */
@media (max-width: 1023px) {
  .btn--outline {
    display: none; }
  .btn--blue {
    max-width: 379px;
    min-height: 55px;
    padding: 20px 25px 14px;
    margin-right: 0;
    margin-left: 121px; }
  .btn--question {
    min-width: 282px; } }

/* Mobile */
@media (max-width: 767px) {
  .btn--blue {
    display: block;
    min-width: 287px;
    max-width: none;
    padding: 21px 22px 11px;
    margin-right: auto;
    margin-left: auto; }
  .btn--question {
    width: 100%;
    min-width: 0;
    margin-right: 0; } }

.header .logo img:first-child {
  display: flex; }

.header .logo img:last-child {
  display: none; }

/* Mobile */
@media (max-width: 767px) {
  .header .logo img:first-child {
    display: none; }
  .header .logo img:last-child {
    display: block; } }

.banner {
  position: relative;
  padding: 203px 0 108px 0;
  min-height: 766px;
  overflow: hidden; }

.banner__container {
  position: relative;
  z-index: 1;
  padding-left: 18px; }

.banner__content {
  max-width: 715px; }

.banner__wrapper {
  position: absolute;
  top: -57px;
  left: 805px;
  z-index: -1;
  width: 705px; }
  .banner__wrapper::before {
    content: "";
    position: absolute;
    top: 13px;
    left: 14px;
    z-index: -1;
    width: 611px;
    height: 488px;
    background: url("../img/icon-triangle-banner.svg") left -128px top -164px no-repeat; }

.banner__title {
  max-width: 683px;
  margin: 0 0 27px 0;
  white-space: nowrap;
  text-transform: uppercase;
  letter-spacing: 0.07em;
  word-spacing: normal;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 94px;
  font-weight: bold;
  color: #ffffff; }

.banner__text {
  position: relative;
  margin: 0 0 45px 96px;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 22px;
  line-height: 34px;
  font-weight: normal;
  color: #ffffff; }
  .banner__text::before {
    content: "";
    position: absolute;
    top: 17px;
    left: -94px;
    display: block;
    width: 66px;
    height: 2px;
    background-color: #ffffff; }

.banner__scroll {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  position: absolute;
  top: 419px;
  left: -17px;
  opacity: 0.5;
  transform: rotate(-90deg);
  background: none;
  cursor: pointer;
  font-family: "PT Mono", "Courier New", monospace;
  font-size: 14px;
  line-height: 16px;
  font-weight: normal;
  color: #ffffff; }
  .banner__scroll:hover, .banner__scroll:focus {
    opacity: 1; }

/* Tablet */
