    min-height: 612px;
    padding-top: 116px; }
  .banner__content {
    max-width: 410px; }
  .banner__content h1 {
    margin-bottom: 29px;
    margin-left: 69px;
    font-size: 60px;
    line-height: 70px;
    white-space: normal; }
  .banner__wrapper {
    position: absolute;
    top: -75px;
    left: 510px; }
    .banner__wrapper::before {
      top: -29px;
      left: -56px;
      width: 480px;
      height: 380px;
      background-size: 600px auto;
      background-position: left -93px top -127px; }
  .banner__content p {
